export default {
    name: 'UniImage',
    props: {
        mode: {
            type: String,
            default: 'scaleToFill'
        },
        defaultImage: {
            type: String,
            default: '../static/image/avatar.jpg'
        },
        url: {
            type: String,
            default: ''
        }
    },
    data () {
        return {
            imageSrc: ''
        }
    },
    watch: {
        url: {
            handler(newVal, oldVal) {
                this.imageSrc = newVal
            },
            immediate: true
        }
    },
    methods: {
        imageLoad () {},
        imageErr () {
            this.url && (this.imageSrc = this.defaultImage)
            this.$forceUpdate()
        }
    }
}
